import { combineReducers } from "redux";
const ContentReducer = (state = [], action) => {
  switch (action.type) {
    case "ALL_CONTENT":
      return action.data.map((e) => {
        e.filter = false;
        return e;
      });
    case "SHOW_CONTENT":
      return state.map((e) => {
        e.filter = false;
        return e;
      });
    case "FILTER_CONTENT":
      let result = state.map((e) => {
        if (e.ComponentType !== action.filterType) {
          e.filter = true;
        } else {
          e.filter = false;
        }
        return e;
      });

      return result;
    case "SEACTH_CONTENT":
      let result2 = state.map((e) => {
        if (e.ComponentTitle.includes(action.title)) {
          e.filter = false;
        } else {
          e.filter = true;
        }
        return e;
      });
      return result2;
    default:
      return state;
  }
};
let inisitalState = {
  list: [],
  loading: false,
};
const TypeReducer = (state = inisitalState, action) => {
  switch (action.type) {
    case "GET_TYPE":
      return Object.assign({}, state, {
        list: action.list,
      });
    case "LADING_CONTENN":
      return Object.assign({}, state, {
        loading: action.loading,
      });
    default:
      return state;
  }
};

const ComponentApp = combineReducers({
  ContentReducer,
  TypeReducer,
});

export default ComponentApp;
